.phone-input {
  max-width: 100%;
  width: 470px;
  height: 60px;
  border-radius: 56px;
  margin-top: 10px;

  border: 1px solid #c4c4c4;
  box-shadow: inset 0 2px 1px rgba(0, 0, 0, 0.25);

  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #090808;
  padding-left: 18px;
}

.phone-input:focus {
  border: 1px solid #FCBE24;
  outline: none;
}

@media (max-width: 550px) {
  .phone-input {
    width: auto;
  }
}
